.foot{
    display: flex;
}

#log{
    padding: 5%;
    
}
#log h2{
    color: #756a69;
    
}
#log i{
    padding-top: 20px;
    padding-left: 55px;

}
.foot .footad{
    display: flex;
    padding: 5%;
}
.foot .footad{
    right: 0;
}
.foot .footad ul li{
    color: #756a69;
list-style-type: none;
line-height: 3;
padding-right: 90px;

}
.foot .footad ul li strong{
    padding-bottom: 5px;
    color:#685f5e ;
}
.foot .footad ul li a{
    border:1px solid#756a69 ;
    padding: 9px;
    border-radius: 4px;
}